// React
import React from 'react';
// MUI
import BottomNavigation from '@mui/material/BottomNavigation';
import BottomNavigationAction from '@mui/material/BottomNavigationAction';

// Icons
import CompareArrowsRoundedIcon from '@mui/icons-material/CompareArrowsRounded';
import HomeRoundedIcon from '@mui/icons-material/HomeRounded';
import CalendarTodayRoundedIcon from '@mui/icons-material/CalendarTodayRounded';
import AddCircleRoundedIcon from '@mui/icons-material/AddCircleRounded';

type SculisScreen = "dashboard" | "schedule" | "substitution"

type SculisBottomNavigationProps = {
    dashboardHandler: Function;
    scheduleHandler: Function;
    substitutionHandler: Function;
}

/**
 * Bottom part of the app. Provides navigation handling.
 * @param props 
 * @returns BottomNavigation > BottomNavigationAction > HomeRoundedIcon
 */
export default function SculisBottomNavigation(props: SculisBottomNavigationProps) {
    const [screen, setScreen] = React.useState<SculisScreen>('dashboard');

    // ScheduleScreen NavigationAction<'schedule'> style
    type ScheduleNavigationActionState = {
        icon: JSX.Element
    }

    const scheduleNavigationActionDefault = {
        icon: <CalendarTodayRoundedIcon />
    }

    const scheduleNavigationActionOnScheduleScreen = {
        icon: <AddCircleRoundedIcon fontSize={"large"} height={"60px"} />
    }

    const [scheduleNavigationAction, setScheduleNavigationAction] = React.useState<ScheduleNavigationActionState>(scheduleNavigationActionDefault)

    function setScheduleScheduleScreen() {
        if (scheduleNavigationAction !== scheduleNavigationActionOnScheduleScreen) {
            setScheduleNavigationAction(scheduleNavigationActionOnScheduleScreen);
        }
    }

    function setScheduleDefault() {
        if (scheduleNavigationAction !== scheduleNavigationActionDefault) {
            setScheduleNavigationAction(scheduleNavigationActionDefault);
        }
    }


    const handleScreenChange = (event: React.SyntheticEvent, newScreen: SculisScreen) => {
        setScreen(newScreen);

        if (newScreen === 'dashboard') {
            props.dashboardHandler();
            setScheduleDefault();

        } else if (newScreen === 'schedule') {

            props.scheduleHandler();
            setScheduleScheduleScreen();


        } else if (newScreen === 'substitution') {
            props.substitutionHandler();
            setScheduleDefault();
        }
    };

    return (
        <BottomNavigation value={screen} onChange={handleScreenChange} >

            <BottomNavigationAction
                {...scheduleNavigationAction}
                value="schedule"
                label={"."}
                disableRipple
            />
            <BottomNavigationAction
                label="."
                value="dashboard"
                icon={<HomeRoundedIcon />}
                disableRipple
            />

            <BottomNavigationAction
                label="."
                value="substitution"
                icon={<CompareArrowsRoundedIcon />}
                disableRipple
            />
        </BottomNavigation>
    )
}