import React from 'react';
import { useTheme } from '@mui/material/styles';

import { AppBar, Box, Button, Card, Container, IconButton, List, Paper, Switch, Toolbar, Typography, TextField, alpha, TableContainer, Theme } from '@mui/material';

import { LocalizationProvider, StaticDatePicker } from '@mui/lab';
import AdapterDateFns from '@mui/lab/AdapterDateFns';
import { de } from 'date-fns/esm/locale'



import MenuIcon from '@mui/icons-material/Menu';
import FilterListRoundedIcon from '@mui/icons-material/FilterListRounded';
import SearchIcon from '@mui/icons-material/SearchRounded';

import { styled } from '@mui/material/styles';


type SubbarVariant = "search-bar" | "filter-bar" | "date-picker-bar" | null;

const RowList = styled(List)(({ theme }) => ({
    display: "flex",
    flexDirection: "row",
    alignItems: "center",
    justifyContent: "space-between"
}));

export default function SculisAppBar() {
    const [subbar, setSubBar] = React.useState<SubbarVariant>(null);

    function switchToSubBar(sbbr: SubbarVariant) {
        if (subbar !== sbbr) {
            setSubBar(sbbr)
        } else {
            setSubBar(null)
        }
    }

    function switchToDatePickerSubBar() {
        switchToSubBar('date-picker-bar')
    }

    function switchToSearchSubBar() {
        switchToSubBar('search-bar')
    }

    function switchToFilterSubBar() {
        switchToSubBar('filter-bar')
    }


    return (
        <Box>
            <AppBar position="static">
                <Container>
                    <Toolbar disableGutters sx={{ justifyContent: "space-between" }}>
                        <RowList>
                            <IconButton
                                edge="start"
                                color="inherit"
                            >
                                <MenuIcon />
                            </IconButton>
                            <Button onClick={switchToDatePickerSubBar} sx={{ marginLeft: "10px" }}>
                                {"MONTAG 12. SEP"}
                            </Button>
                        </RowList>

                        <RowList sx={{ right: "0px", }}>
                            <IconButton
                                edge="end"
                                color="inherit"
                                sx={{ marginRight: "10px" }}
                                onClick={switchToFilterSubBar}
                            >
                                <FilterListRoundedIcon />
                            </IconButton>
                            <IconButton
                                edge="end"
                                color="inherit"
                                onClick={switchToSearchSubBar}
                            >
                                <SearchIcon />
                            </IconButton>
                        </RowList>
                    </Toolbar>
                </Container>
            </AppBar>
            <SubBar subheader={subbar} />
        </Box >
    )
};

type SubBarProps = {
    subheader: SubbarVariant;
}

const SubBar = (props: SubBarProps) => {
    return (
        <Container sx={{
            position: "absolute", zIndex: 10, backgroundColor: "rgba(225, 255, 225, .3)",
            backdropFilter: "blur(10px)", boxShadow: "0 4px 6px " + useTheme().palette.primary.main
        }} disableGutters>
            {
                props.subheader === 'filter-bar' ? (
                    <Paper sx={{ paddingY: "8px" }}><FilterSubBar /></Paper>
                ) : props.subheader === 'date-picker-bar' ? (

                    <DatesSubBar />
                ) : props.subheader === 'search-bar' ? (
                    <SearchSubBar />
                ) : null
            }
        </Container>
    )
};

const DatesSubBar = () => {
    // types
    type DateValueType = Date | null | undefined

    // states
    const [dateValue, setDateValue] = React.useState<DateValueType>(new Date());

    // ################## CONSTANTS ##################
    // allowed date-range
    const minDate = new Date(2022, 0, 21)
    const maxDate = new Date()
    maxDate.setDate(maxDate.getDate() + 1)

    return (

        <Container disableGutters>

            <LocalizationProvider dateAdapter={AdapterDateFns} locale={de}>
                <StaticDatePicker
                    value={dateValue}
                    onChange={(newValue: DateValueType) => {
                        setDateValue(newValue);
                    }}



                    minDate={minDate}
                    maxDate={maxDate}

                    open={false}
                    openTo="day"

                    displayStaticWrapperAs="mobile"
                    disableCloseOnSelect={true}

                    onAccept={() => { }}

                    toolbarTitle={"Vertretung fr"}


                    renderInput={(params) => <TextField sx={{ backgroundColor: 'black' }} {...params} />}
                    views={['day']}
                />
            </LocalizationProvider>
        </Container >
    )
}

const FilterSubBar = () => {
    return (

        <Container sx={{
            display: 'flex',
            flexDirection: 'row',
            justifyContent: "space-between",
            alignItems: 'center'
        }}>
            <Card variant="outlined"
                sx={{
                    borderRadius: 999,
                    display: "flex",
                    justifyContent: "space-between",
                    alignItems: "center",
                }}>
                <Typography sx={{ margin: "8px" }}>
                    {"Zeige Uhrzeiten"}
                </Typography>
                <Switch />
            </Card>
            <Button>
                {"Q1"}
            </Button>

        </Container>


    )
};

const SearchSubBar = () => {
    return (
        null
    )
};